.event-card {
  color: #ffffff;
  background-image: url(../img/events__bg.jpg);
  background-size: cover;
  background-repeat: no-repeat;
  background-position: center;
  box-shadow: 0 4px 4px rgba(0, 0, 0, 0.25);
  height: auto;
}

.event-card__top {
  display: flex;
  padding: 20px 24px 0;
  align-items: center;
}

@media screen and (max-width: 767px) {
  .event-card__top {
    padding: 16px 20px 0;
    flex-wrap: wrap;
  }
}

.event-card__date {
  display: flex;
  margin-right: 48px;
}

@media screen and (max-width: 767px) {
  .event-card__date {
    margin-right: 32px;
  }
}

@media screen and (max-width: 767px) {
  .event-card__top-item.--format {
    flex: 0 0 100%;
    margin-top: 32px;
  }
}

.event-card__top-item + .event-card__top-item {
  margin-left: 48px;
}

@media screen and (max-width: 639px) {
  .event-card__top-item + .event-card__top-item {
    margin-left: 32px;
  }
}

.event-card__top-title {
  font-size: 14px;
  display: block;
}

.event-card__top-value {
  display: block;
  font-size: 36px;
  margin-top: 4px;
  font-weight: 600;
  line-height: 123%;
}

@media screen and (max-width: 767px) {
  .event-card__top-value {
    font-size: 20px;
  }
}

.event-card__link {
  display: block;
  padding: 10px 16px;
  border: 2px solid #ffffff;
  color: #ffffff;
  text-decoration: none;
  border-radius: 35px;
  font-weight: 300;
  font-size: 14px;
  line-height: 1;
  transform-origin: center center;
  transition: all ease-out 240ms;
}

.event-card__link:hover {
  transform: scale(1.05);
}

.event-card__link:focus {
  outline: transparent;
}

.event-card__link:focus-visible {
  transform: scale(1.05) translateY(-5%);
}

.event-card__link:active {
  transform: scale(0.95);
}

.event-card__link.--top {
  margin-left: 24px;
}

@media screen and (max-width: 767px) {
  .event-card__link.--top {
    margin-left: 0;
    margin-top: 16px;
  }
}

.event-card__title {
  padding: 0 24px 16px;
  border-bottom: 3px solid #CDCDCD;
  font-weight: 600;
  font-size: 24px;
}

@media screen and (max-width: 639px) {
  .event-card__title {
    font-size: 20px;
  }
}

.event-card__descr {
  margin-top: 16px;
  padding: 0 24px;
  font-size: 14px;
}

.event-card__tags {
  padding: 0 24px;
  margin-top: 48px;
  display: flex;
  justify-content: flex-start;
  align-items: start;
  list-style: none;
  flex-wrap: wrap;
  margin-left: -4px;
}

.event-card__tag-item {
  margin: 4px;
}

.event-card__tag-link {
  display: block;
  padding: 5px 10px;
  border: 1px solid #ffffff;
  color: #ffffff;
  text-decoration: none;
  border-radius: 35px;
  font-weight: 300;
  font-size: 12px;
  line-height: 1;
  white-space: nowrap;
  transform-origin: center center;
  transition: all ease-out 240ms;
}

.event-card__tag-link:hover {
