}

.event-card__tag-link:focus {
  outline: transparent;
}

.event-card__tag-link:focus-visible {
  transform: scale(1.05) translateY(-5%);
}

.event-card__tag-link:active {
  transform: scale(0.95);
}

.event-card__bottom {
  margin-top: 24px;
  padding: 0 24px 20px;
  display: flex;
  justify-content: space-between;
  align-items: center;
}

